<script>
import { defineComponent, watch, computed, ref, onMounted, nextTick } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useUser } from 'composable/user'
import { useStatuses, useCategories, useBuybacks, usePaymentMethods, usePrint, useBulkArchive, useBulkUnarchive, useExport } from 'composable/buyback'
import { useCompanyProfile } from 'composable/setting'
import { useNotification } from 'composable/notification'

import { color } from 'utils/status'
import { currency } from 'utils/number'
import debounce from 'utils/debounce'
import { nameCapitalize } from 'utils/element'
import { formatDatabase } from 'utils/date'

import Tab from 'bbUi/components/tab/tab.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import GroupContainer from 'bbUi/components/group-container/group-container.vue'
import MarketPlaceFilter from 'components/Buyback/MarketPlaceFilter.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Pagination from 'components/Pagination.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Currency from 'components/Currency.vue'
import TextLimit from 'components/TextLimit.vue'
import SelectableChip from 'components/SelectableChip.vue'
import CustomTableManager from 'components/CustomTable/CustomTableManager.vue'
import CustomTableExport from 'components/CustomTable/CustomTableExport.vue'
import FilterDropdown from 'components/FilterDropdown.vue'
import BTable from 'components/BTable/BTable.vue'

import { ElMessage } from 'element-plus'
import ExpeditedIcon from 'components/ExpeditedPayout/ExpeditedIcon.vue'

export default defineComponent({
  components: {
    Tab,
    Badge,
    GroupContainer,
    MarketPlaceFilter,
    Button,
    Input,
    Checkbox,
    Select,
    Pagination,
    Modal,
    Currency,
    TextLimit,
    SelectableChip,
    CustomTableManager,
    CustomTableExport,
    BTable,
    FilterDropdown,
    ExpeditedIcon

  },
  props: {
    customer: {
      type: Boolean,
      default: false
    }
  },
  async setup(props) {
    const { tab, state: stateStatuses, action, actionStatusOptions } = useStatuses('local', props.customer)
    const { tabInStore: categoriesTab } = useCategories()
    const { me } = useUser()
    const { state: statePaymentMethods, loading: loadingPaymentMethods, action: actionPaymentMethods } = usePaymentMethods('local')
    const {
      data,
      isDateShow,
      state: table,
      all,
      filterList,
      clearFilter,
      clearAllFilter,
      isFiltered,
      filterDateRef,
      filterDateOptions,
      filterDateFocus,
      loading,
      modal,
      actionDelete,
      actionHardDelete,
      actionExportPaypalMass,
      openModal,
      isAvailableMasspay,
      masspayPayoutStatus,
      masspayPayoutLabel
    } = useBuybacks('local', props.customer)
    const { loading: loadingPrint, actionBatchCheck: printBatchCheck } = usePrint('local')
    const { data: bulkArchiveData, modal: bulkArchiveModal, loading: bulkArchiveLoading, actionBulkArchive, unsetData: bulkArchiveUnsetData } = useBulkArchive('online')
    const { data: bulkUnarchiveData, modal: bulkUnarchiveModal, loading: bulkUnarchiveLoading, actionBulkUnarchive, unsetData: bulkUnarchiveUnsetData } = useBulkUnarchive('online')
    const { loading: loadingExport, extension: exportExtension, actionCSV: actionExportCSV, actionCSVTremendous: actionExportCSVTremendous, actionCSVDOTS: actionExportCSVDOTS } = useExport('local')
    const { companyProfile } = useCompanyProfile()
    const {
      exportDataOffersProgress,
      resetExportDataOffersProgress
    } = useNotification()
    const route = useRoute()
    const router = useRouter()
    const selected = ref(null)
    const isDelete = ref(false)
    const selectedBuybacks = ref([])
    const refBuybackTable = ref(null)
    const exportInProcess = ref(table.value.is_csv_download == 1)
    const isAppliedFilter = ref(false)
    const isLoadingDropdownFilter = ref(false)

    const filterExport = computed(() => {
      const filter = { ...data }
      delete filter.page
      delete filter.limit

      return filter
    })
    const isPendingModal = computed(() => Object.entries(modal).filter(([modalKey, modalValue]) => modalValue === true).length > 0)
    const subStatuses = computed(() => {
      const statusId = route.params.statusId
      const status = stateStatuses.value.find(status => status.id == statusId)
      if(!status) return []
      if(!status.hasOwnProperty('sub')) return []
      return status.sub.map(subStatus => {
        if(subStatus.id === 1000){
          return {
            ...subStatus,
            color: 'orange'
          }
        } else if (subStatus.id === 1){
          return {
            ...subStatus,
            color: 'green'
          }
        }else{
          return {
            ...subStatus,
            color: 'teal'
          }
        }
      })
    })
    const hasSubStatus = computed(() => subStatuses.value.length > 0)
    const isMailInCurrentLocation = computed(() =>  me.value.is_location_mail_in === 1)
    // REMOVE MARKETPLACE FEATURE
    // const partners = computed(() => {
    //   if (table.value && table.value.partner && table.value.partner.list && table.value.partner.list.length) {
    //     return table.value.partner.list
    //   }
    //   return []
    // })
    // REMOVE MARKETPLACE FEATURE
    const showFilterMarketplace = computed(() => ({
      partner: (table.value && table.value.marketer && table.value.marketer.is_enable) || false,
      marketer: (table.value && table.value.partner && table.value.partner.is_enable) || false
    }))
    const showPartnerTableHead = computed(() => {
      const item = table.value.data && table.value.data.length ? [...table.value.data].shift() : undefined
      const keys = item ? Object.keys(item) : []
      return keys.includes('partner_name')
    })
    const isUnlinkMarketplace = computed(() => {
      return data.marketer && data.marketer.linking_status && (data.marketer.linking_status === 'unlink' || data.marketer.linking_status === 'deleted')
    })
    const currentBuybackStatusName = computed(() => {
      const isOfferStatusNotActive = route.params.category?.toLowerCase?.() !== 'active'
      if(isOfferStatusNotActive) return 'Archive'
      if(route.params.statusId === 'all') return 'All'
      if(hasSubStatus.value) {
        const currentSubStatus = subStatuses.value.find(subStatus => subStatus.id == route.params.subStatusId)
        return currentSubStatus?.short_name || ''
      }
      return stateStatuses.value.find(status => status.id == route.params.statusId)?.short_name || ''
    })
    const exportFilename = computed(() => {
      const statusName = currentBuybackStatusName.value
      const [fromDate, toDate] = data.date || []
      const hasDateFilter = fromDate && toDate
      if(!hasDateFilter) return statusName
      const formattedFromDate = fromDate ? formatDatabase(fromDate) : ''
      const formattedToDate = toDate ? formatDatabase(toDate) : ''
      return `${statusName} ${formattedFromDate} to ${formattedToDate}`
    })
    const isMarketplaceFilterApplied = computed(() => !!data.marketer?.id)

    const fetchAll = async() => {
      if(isMailInCurrentLocation.value) return false
      if(route.name !== 'buybackLocalTab') return false
      resetExportDataOffersProgress()

      let customerId = null
      let statusId = !!route.params.subStatusId ? route.params.subStatusId : route.params.statusId
      const isArchive = route.params.category === 'archive'
      const isEmptyStatusId = !statusId && !isArchive

      if(isEmptyStatusId) {
        return router.push({
          name: 'buybackLocalTab',
          params: {
            statusId: 'all',
            category: 'active'
          }
        })
      }

      if(isArchive) statusId = 'archive'
      if(route.name === 'customerDetail' && props.customer) customerId = route.params.customerId
      if((route.name === 'buybackLocalTab' && !props.customer) || (route.name === 'customerDetail' && props.customer)) {
        await action({
          ...data,
          status_id: route.params.statusId,
          customer_id: customerId,
          is_expired:
            !!route.params.category ?
              route.params.subStatusId && route.params.subStatusId.toLowerCase() === 'expired' ? 1
                : 0
              : 0,
          selling_type: data.selling_type.value
        })
        await all(statusId, customerId)
      }

      selectedBuybacks.value = []
      exportInProcess.value = table.value.is_csv_download == 1
    }

    watch(() => ({
      route: route.params.statusId,
      limit: data.limit,
      search: data.search,
      payment_method: data.payment_method,
      date: data.date
    }), () => {
      data.page = 1
    }, {
      deep: true
    })
    watch(() => ({
      route: route.params.statusId,
      page: data.page,
      limit: data.limit,
      search: data.search,
      marketplace: data.marketer
      // ...data
    }), debounce(fetchAll, 500), {
        deep: true
    })
    watch(() => hasSubStatus.value, (hasSubStatus) => {
      if(hasSubStatus){
        let subStatus = subStatuses.value[0]?.id
        const isLeads = route.params.statusId == 1001
        if(isLeads) subStatus = 1 // set default sub status to active leads when status is leads

        const hasPredefinedSubStatusId = !!route.params.subStatusId
        if(hasPredefinedSubStatusId) subStatus = route.params.subStatusId

        return router.push({
          name: 'buybackLocalTab',
          params: {
            statusId: route.params.statusId,
            subStatusId: subStatus
          }
        })
      }
    })
    watch(() => exportDataOffersProgress.value, async (val) => {
      const routeParamsId = (route?.params?.subStatusId && route.params.subStatusId.trim() !== '')
        ? route.params.subStatusId
        : route?.params?.statusId
      if (val?.type === 'in-store' && val?.status_export === 1 && val?.status_id === routeParamsId) {
        exportInProcess.value = val?.is_csv_download == 0 ? false : true
        await nextTick()
        resetExportDataOffersProgress()
      }
    }, { deep: true, immediate: true })

    const statusId = computed(() => {
      return route.params.statusId
    })
    const formattedDateFilter = computed(() => {
      const formattedDate = {
        from: null,
        to: null
      }
      const [fromDate, toDate] = data.date || []
      const hasDateFilter = !!fromDate && !!toDate
      if(!hasDateFilter) return formattedDate
      formattedDate.from = formatDatabase(fromDate) || null
      formattedDate.to = formatDatabase(toDate) || null
      return formattedDate
    })
    const dropdownFilterOptions = computed(() => {
      const filters = [
        {
          name: 'Select Date',
          key: 'date',
          count_selected: 0
        },
        {
          name: 'Payout Method',
          key: 'payout_method',
          count_selected: (data.payment_method || []).length || 0
        },
        // REMOVE MARKETPLACE FEATURE
        // {
        //   name: 'Partners',
        //   key: 'partner',
        //   count_selected: 0
        // }
      ]
      // REMOVE MARKETPLACE FEATURE
      // if(showFilterMarketplace.value.partner && partners.value.length) filters.push({
      //  name: 'Partners',
      //  key: 'partner',
      //  count_selected: 0
      // })
      return filters
    })
    const isNotAppliedFilters = computed(() => {
      const appliedFilters = filterList.value.filter((filter) => !!filter.value)
      return appliedFilters.length === 0
    })

    const tableSummary = ({columns}) => {
      return columns.map(column => column.property === 'sub_total' ?  `${currency(me.value, table.value.total_displayed)}` : '' || column.property === 'qty' ? 'Total' : '')
    }
    const redirectToOrder = (row) => {
      if(isDelete.value || !row.offer_id || isPendingModal.value) return
      router.push({
        name: 'buybackLocalDetail',
        params: {
          buybackId: row.offer_id
        }
      })
    }

    const deleteOrder = (row) => {
      const dataOrder = {
        id: selected.value.id,
        status_id: route.params.statusId,
        customer_id: props.customer || null,
        box_id: selected.value.offer_id
      }
      actionDelete(dataOrder)
    }

    const openModalDelete = (row) => {
      isDelete.value = true
      selected.value = row
      openModal('delete')
    }
    const openModalHardDelete = (row) => {
      selected.value = row
      openModal('hardDelete')
    }

    const closedModalDelete = () => isDelete.value = false
    const tableOnSelect = (buybacks) => {
      selectedBuybacks.value = buybacks
    }
    const getRowKey = (row) => row.id
    const selectableRow = (row) => {
      return true
    }
    const handleSetPartner = (partner) => {
      data.partner.value = partner.id
      data.partner.label = partner.name
    }
    const handleArchiveModal = () => {
      bulkArchiveModal.value = true;
    }

    const handleCloseArchiveModal = () => {
      bulkArchiveModal.value = false;
    }

    const handleUnarchiveModal = () => {
      bulkUnarchiveModal.value = true;
    }

    const handleCloseUnarchiveModal = () => {
      bulkUnarchiveModal.value = false;
    }
    const handleChangeSubStatus = (subStatusId) => {
      router.push({
        name: 'buybackLocalTab',
        params: {
          statusId: route.params.statusId,
          category: route.params.category,
          subStatusId: subStatusId
        }
      })
    }

    const archiveOrder = () => {
      bulkArchiveData.selected_order_id = JSON.stringify(selectedBuybacks.value.map(buyback => buyback.order_id));
      actionBulkArchive(() => {
        ElMessage({
          type: 'success',
          message: 'Success archiving offer'
        })
        handleCloseArchiveModal()
        // refBuybackTable.value.clearSelection()
        selectedBuybacks.value = []
        // all(route.params.statusId, route.params?.customerId)
        fetchAll()
        bulkArchiveUnsetData()
      })
    }

    const unarchiveOrder = () => {
      bulkUnarchiveData.selected_order_id = JSON.stringify(selectedBuybacks.value.map(buyback => buyback.order_id));
      actionBulkUnarchive(() => {
        ElMessage({
          type: 'success',
          message: 'Success restoring offer'
        })
        handleCloseUnarchiveModal()
        // refBuybackTable.value.clearSelection()
        selectedBuybacks.value = []
        // all('archive', route.params?.customerId)
        fetchAll()
        bulkUnarchiveUnsetData()
      })
    }

    const handleHardDeleteOrder = async () => await actionHardDelete(selected.value, async () => {
      selected.value = null
      modal.hardDelete = false

      ElMessage({
        type: 'success',
        message: 'Offer deleted'
      })

      // await all(route.params.statusId, route.params?.customerId)
      await fetchAll()
    })
    const exportPaypalMass = async (e) => {
      await actionExportPaypalMass(route.params.statusId, route.params.category === 'expired' ? '1' : '0', e)
    }
    const exportCSV = async (e) => {
			exportExtension.value = e?.extension || e?.ext || e
			if (e.isTremendous)
				await actionExportCSVTremendous()
			else {
				await actionExportCSV({
					status_id: route.params.subStatusId
						? route.params.subStatusId
						: route.params.statusId,
					from: formattedDateFilter.value.from,
					to: formattedDateFilter.value.to,
          // all filter table except page and limit
          ...filterExport.value,
          // all filter table except page and limit
          selling_type: data.selling_type.value,
					is_grouped: e?.grp
				})
				ElMessage({
					type: 'success',
					message:
						'Your export data request is processing, it will be delivered to your email when the process is completed.'
				})
				exportInProcess.value = true
				exportExtension.value = 'csv'
			}
    }
    const exportCSVDOTS = async (e) => {
      const payload = {
        payment_method: e.payouts.map(i => i.name),
        delivery_method: e.dots,
        extension: e.extension
      }
      await actionExportCSVDOTS(payload, `${e.extension.toUpperCase()} DOTS`)
      ElMessage({
        type: 'success',
        message: 'Your export data has been sent'
      })
      exportInProcess.value = true
    }
    const handleUpdateCustomTable = async () => {
      await fetchAll()
    }
    const handleApplyFilter = async () => {
      isLoadingDropdownFilter.value = true
      isAppliedFilter.value = true
      await fetchAll()
      isLoadingDropdownFilter.value = false
    }
    const handleResetFilter = async () => {
      isLoadingDropdownFilter.value = true
      isAppliedFilter.value = false
      clearAllFilter()
      await fetchAll()
      isLoadingDropdownFilter.value = false
    }
    const handleDropdownItemClick = (item) => {
      if (!item) return
      if (item.key === 'date') filterDateFocus()
    }
    const handleClearFilter = async (filter) => {
      clearFilter(filter)
      await handleApplyFilter()
      if(isNotAppliedFilters.value) isAppliedFilter.value = false
    }

    const dropdownItemFilterLoading = () => {
      return (
        loading.getFilterOptions || loading.all || loadingPaymentMethods.value
      )
    }
    const dropdownItemFilterDisableHover = (filter) => {
      return filter.key === 'date'
    }

    if(isMailInCurrentLocation.value){
      const isCurrentRouteActiveLeads = route.params.statusId == 1001 && route.params.subStatusId == 1
      const isCurrentRouteAbandonedBuyback = route.params.statusId == 1001 && route.params.subStatusId == 1000

      let redirectRouteParams = {
        category: 'active',
        statusId: 'all',
        subStatusId: ''
      }
      if(isCurrentRouteActiveLeads){
        redirectRouteParams.statusId = 1001
      }
      if(isCurrentRouteAbandonedBuyback){
        redirectRouteParams.statusId = 1001
        redirectRouteParams.subStatusId = 1000
      }

      router.push({
        name: 'buybackOnlineTab',
        params: redirectRouteParams
      })
    }

    onMounted(async () => {
      await actionStatusOptions()
      await fetchAll()
      await actionPaymentMethods()
    })

    return {
      me,
      data,
      isDateShow,
      loading,
      loadingPrint,
      loadingExport,
      table,
      tab,
      filterList,
      isFiltered,
      companyProfile,
      statePaymentMethods,
      loadingPaymentMethods,
      actionPaymentMethods,
      filterDateRef,
      filterDateOptions,
      filterDateFocus,
      color,
      clearFilter,
      clearAllFilter,
      statusId,
      tableSummary,
      redirectToOrder,
      route,
      modal,
      deleteOrder,
      openModalDelete,
      closedModalDelete,
      printBatchCheck,
      categoriesTab,
      tableOnSelect,
      selectedBuybacks,
      getRowKey,
      selectableRow,
      tableConfig: {
        isShowSelectionAll: true
      },
      bulkArchiveModal,
      bulkUnarchiveModal,
      handleSetPartner,
      handleArchiveModal,
      handleUnarchiveModal,
      handleCloseArchiveModal,
      handleCloseUnarchiveModal,
      archiveOrder,
      unarchiveOrder,
      bulkArchiveLoading,
      bulkUnarchiveLoading,
      refBuybackTable,
      exportInProcess,
      isPendingModal,
      subStatuses,
      hasSubStatus,
      // partners,
      isUnlinkMarketplace,
      showFilterMarketplace,
      showPartnerTableHead,
      nameCapitalize,
      openModalHardDelete,
      handleHardDeleteOrder,
      handleChangeSubStatus,
      exportPaypalMass,
      exportCSV,
      exportCSVDOTS,
      isAvailableMasspay,
      masspayPayoutStatus,
      masspayPayoutLabel,
      handleUpdateCustomTable,
      exportExtension,
      exportFilename,
      isMarketplaceFilterApplied,
      isLoadingDropdownFilter,
      isAppliedFilter,
      dropdownFilterOptions,
      handleApplyFilter,
      handleResetFilter,
      handleDropdownItemClick,
      handleClearFilter,
      dropdownItemFilterLoading,
      dropdownItemFilterDisableHover
    }
  }
})
</script>

<style lang="scss" scoped>
.el-popover.el-popper{
  width: max-content !important;
}
.substatus-chip{
  .chip{
    &.--selected{
      .substatus-counter{
        &.--color-teal {
          @apply bg-teal-04;
        }
        &.--color-orange{
          @apply bg-orange-04
        }
        &.--color-red{
          @apply bg-red-04;
        }
        &.--color-green{
          @apply bg-green-04;
        }
      }
    }
  }
}

:deep(.group-container){
  @media screen and (max-width: theme('screens.xl')){
    .group__item{
      @apply w-full #{!important};
      .btn,
      .el-dropdown{
        @apply w-full #{!important};
      }
      .btn{
        border-radius: 0 !important;
        border: solid rgba(0,0,0,0.06) !important;
      }
    }
  }
}
</style>
